
@import url(style/common.css);
#app{font-family:'Avenir', Helvetica, Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-align:center;color:#2c3e50;margin-top:60px;position:relative
}
#app .final_price{width:100%;height:40px;position:fixed;background:rgba(0,0,0,0.7);top:0;font-size:30px;line-height:40px;color:white;z-index:1
}
.order_list{width:100%
}
.blank{height:40px
}
.show_order{color:white;background:rgba(200,100,111,0.8);position:fixed;bottom:5px;right:5px;height:40px;line-height:40px;width:100px
}
.ordered{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:80%;height:80%;background:white;color:black;overflow:scroll
}



